/******************************************************************
	
Stylesheet: Main Stylesheet

Here's where the magic happens. Here is where you import
all of your Sass files so they can compile into one
CSS file. 

******************************************************************/

// Import fonts
$font-path: "../fonts";

@font-face {
    font-family: "DINAlternateBold";
    src: url("#{$font-path}/din-alternate-bold.ttf");
    src: url("#{$font-path}/din-alternate-bold.ttf?#iefix") url("#{$font-path}/din-alternate-bold.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "DINCondensedBold";
    src: url("#{$font-path}/din-condensed-bold.ttf");
    src: url("#{$font-path}/din-condensed-bold.ttf?#iefix") url("#{$font-path}/din-condensed-bold.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

// Import Kanakuk icon font
$font-path: "../fonts/kanakuk";

@font-face {
    font-family: "kanakuk";
    src: url("#{$font-path}.eot");
    src: url("#{$font-path}.eot?#iefix") format("embedded-opentype"),
    url("#{$font-path}.woff") format("woff"),
    url("#{$font-path}.ttf") format("truetype"),
    url("#{$font-path}.svg#fontcustom") format("svg");
    font-weight: normal;
    font-style: normal;
}

@import "../fonts/kanakuk-icon-font-styles.css";

